.container {
    --bg: #F0E8DB;
    --gradient: radial-gradient(circle at 100% 100%, var(--bg) 0, var(--bg) 4px, transparent 4px) 0% 0%/7px 7px no-repeat,
    radial-gradient(circle at 0 100%, var(--bg) 0, var(--bg) 4px, transparent 4px) 100% 0%/7px 7px no-repeat,
    radial-gradient(circle at 100% 0, var(--bg) 0, var(--bg) 4px, transparent 4px) 0% 100%/7px 7px no-repeat,
    radial-gradient(circle at 0 0, var(--bg) 0, var(--bg) 4px, transparent 4px) 100% 100%/7px 7px no-repeat,
    linear-gradient(var(--bg), var(--bg)) 50% 50%/calc(100% - 6px) calc(100% - 14px) no-repeat,
    linear-gradient(var(--bg), var(--bg)) 50% 50%/calc(100% - 14px) calc(100% - 6px) no-repeat,
    linear-gradient(90deg, #ead35c 0%, #EA8F5C 100%);


    display: flex;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    height: 14px;
    background: var(--gradient);
    border-radius: 7px;
    padding: 3px;
    box-sizing: border-box;
}

.item {
    position: relative;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: background 0.3s ease-in-out, transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out, height 0.3s ease-in-out, width 0.3s ease-in-out;

    color: #301515;
    font-family: Fira Sans Condensed;
    font-size: max(12px, 1.667vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.item::before {
    content: attr(data-title);
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translate(-50%, -100%);
    white-space: nowrap;
}

.item:not(.checked):hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.circle::after {
    content: "";
    position: absolute;
    display: block;
    width: 24px;
    height: 24px;
    border: 2px solid #E8BC4D;
    border-radius: 50%;
    box-sizing: border-box;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: var(--bg);
}

.circle.checked::after {
    height: 38px;
    width: 38px;
    filter: drop-shadow(0px 0px 30px #F19D3B);
}

.grad.checked {
    position: relative;
    background: linear-gradient(90.21deg, #F1DF3B 4.47%, #D8F13B 26.4%, #F19E3B 95.32%);
    border-radius: 10px;
    height: calc(100% + 6px);
    z-index: 2;
    pointer-events: none;
}

.grad.checked::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, #F1DF3B 4.47%, #D8F13B 26.4%, #F19E3B 95.32%);
    filter: blur(5px);
    z-index: 1;
}